
#order-section {
    padding: 10px 100px;
    min-height: 84vh;
  }
  
  #search-order-wrapper {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
  }
  
  #order-section > h1 {
    font-size: 2rem;
    font-weight: 700;
  }
  
  #input-search {
    width: 52%;
    padding: 12px 24px;
    font-size: 15px;
    border: 0;
    border-bottom: 2px solid #8c8c8c;
    margin: 0 12px 0 0;
    outline: none;
  }
  
  #reset-btn, #search-btn {
      display: inline-block;
      border-radius: 30px;
      padding: 14px 36px;
      font-size: 12px;
      font-weight: 500;
      letter-spacing: 1px;
      background-color: #20b883;
      color: #fff;
      border: none;
      outline: none;
      cursor: pointer;
      margin-left: 8px;
  }
  
  #table {
    width: 100%;
    border-collapse: collapse;
    text-align: left;
  }
  
  .table-heading {
    padding-left: 20px;
    padding-bottom: 15px;
    font-weight: 700;
    font-size: 14px;
  }
  
  .order-item-wrapper {
    background-color: white;
    display: table-row;
    margin: 0;
    border-color: white;
    border-bottom: 4px solid #f6f7fb;
  }
  
  .table-data {
    background-color: #fff;
    text-align: left;
    padding: 12px 24px;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.8);
    font-weight: 500;
    width: 135px;
  }
  
  .table-data-fade {
    background-color: #fff;
    text-align: left;
    padding: 0 24px;
    font-size: 14px;
    color: #8c8c8c;
    width: 100px;
  }
  
  .table-data > span {
    color: #8c8c8c;
    display: block;
  }
  #form-search{
    margin-bottom: 20px;
  }