#form-section {
    padding-top: 150px;
    width: 100%;
    height: 95vh;
    background-color: #F6F7FB;
  }
  
  #form {
    margin: 0 auto;
    background-color: #fff;
    width: 100%;
    max-width: 500px;
    padding: 32px;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    box-shadow: 0 0 6px #ccc;
    border-radius: 6px;
  }
  
  .form-heading {
    letter-spacing: 1px;
    font-weight: 500;
    margin: 0 0 24px;
    font-size: 2rem;
  }
  
  
  .form-input {
    padding: 16px 2px 2px 1px;
    border: none;
    border-bottom: 2px solid #8c8c8c;
    outline: none;
    font-weight: 400;
    margin-bottom: 16px;
    font-size: 0.9rem;
  }
  
  .form-input::placeholder {
    color: #8c8c8c;
    
  }
  
  #form-btn {
    margin-top: 25px;
    padding: 16px 32px;
    font-size: 0.9rem;
    font-weight: 500;
    letter-spacing: 1px;
    background-color: #20b883;
    color: #fff;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 30px;
  }
  
  .lo-btn {
    display: none;
  }